.page-banner {
    // background-image: url("../images/barley.jpg");
    // background-size: cover;
    // background-repeat: no-repeat;
    background-color: $layout-high-contrast-bg-light;   

    // reducing page banners container
    &-container {
	max-width: 90%;
	margin: 0 auto;
    }

    > .container > .columns {
        min-height: 200px;
    }
}

.page-title {

    h1 {
	    color: $layout-high-contrast-bg-dark;
	    font-size: ms(2);
	    font-weight: normal;
    }

    span {
        display: block;
    }

    .small {
	color: $layout-reduced-contrast-dark;
        font-size: ms(-2);
    }
}

.page-content {
    min-height: 100vh;
}
