.publication-listing {
    @extend .is-clearfix;
    background: $grey-light;
}

.publication {

	figure.image {
		overflow: hidden;
		border-bottom: 1px solid $grey;

		img {
			min-width: 100%;
			height: 200px;
		}
	}

	&-download-confirmation {
		figure.image img {
			max-width: 200px;
			height: auto;
		}

		.file-attr {
			&-label {
				text-align: right;
				font-size: 0.9rem;
				font-weight: 600;
			}


			&-value {
				font-size: 0.9rem;
			}
		}
	}


}