$news-outline-slider-controls-opacity: 0.5;

.highlights-container {
	background-color: lighten($green, 50%);

	.section-controls {
		margin-top: 2rem;
	}
}

a.news-outline {
	@extend .is-unselectable;
	
	
	&-item {
		position: relative;
		padding-bottom: 2rem;
		display: block;
		height: 500px;
		margin: 2.8rem 0;
		padding: 0;
		overflow: hidden;
		background-color: $grey-light;

		transition-property: box-shadow, transform;
		transition-duration: 500ms;
		// @include box-shadow(1);

		&:hover {
			@include box-shadow(4);
			transition-property: box-shadow, transform;
			transition-duration: 500ms;
			transform: translateY(-0.618rem);
			color: initial;

			.news-outline-heading {
				color: $blue;
			}
		}

		.button {
			margin-top: 1rem;
		}

		.padded {
			padding: 1rem;
		}

		.news-outline-tag {
			font-size: ms(-1);
			font-weight: 600;
		}

		figure {
			overflow: hidden;
			height: 245px;
			img {
				min-height: 100%;
				min-width: 100%;
			}
		}

		.news-outline-location {
			position: absolute;
			bottom: 1rem;
			@extend .is-clearfix;
			font-size: 0.9rem;
			border-radius: $radius-large;
			padding: 0.1rem 0.3rem;

			.icon {
				color: $blue;
			}
		}

		.news-outline-date {
			font-size: 0.8rem;
		}
	}
}

#news_outline {

	.paging-item {
		
		.paging-dot {
			width: 8px !important;
			height: 8px !important;
			background-color: $blue !important;
			opacity: $news-outline-slider-controls-opacity;
		}
		
		&:hover .paging-dot {
			opacity: 1;
		}

		&.active .paging-dot {
			transform: scale(1.2);
			background: $blue!important;
			opacity: 1;
		}
	}

	.slider-control-centerleft, .slider-control-centerright {

		a {
			color: $border;
			opacity: $news-outline-slider-controls-opacity;

			&:hover {
				opacity: 1;
			}
		}
	}
	
	.slider-control-centerright {
		right: 1rem !important;
	}

	.slider-control-centerleft {
		left: 1rem !important;
	}
}

.news-grid {

	&-item {
		min-height: 300px;
		position: relative;
		background: transparent;
		

		.news-image {
			overflow: hidden;
			position: absolute;
			display: flex;
			justify-content: center;
			align-items: center;
			
			top: 0;
			bottom: 0;
			left: 0;
			right: 0;

			z-index: -1;
			
			img {
				min-width: 100%;
				min-height: 100%;
			}

			&::after {
				content: '';
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				background-image: linear-gradient(
					rgba(0, 0, 0, 0.5),
					rgba(0, 0, 0, 0.5)
				);
			}
		}

		.news-content {
			color: white;
			position: absolute;
			bottom: 0;
			right: 0;
			left: 0;

			padding: 1rem;
			max-height: 250px;
			overflow: hidden;

			h2 a {
				text-shadow: none;
				z-index: 0;
			}

			a {
				color: white;
				&:hover {
					color: darken(white, 12%);
				}
			}
		}

		.news-heading {
			color: white;
			font-size: $size-large;
		}

		.news-desc {
			max-height: 0px;
			transition-property: max-height;
			transition-duration: 1s;

			overflow: hidden;
		}

		&:hover .news-desc {
			max-height: 150px;
			transition-property: max-height;
			transition-duration: 1s;
		}
	}
}

.news-listing {
	background-color: $grey-light;	

	&--item {
		border-top: 1px solid $grey-light;
		padding: 1rem;
		margin-bottom: .5rem;
		

		img {
			max-width: 75%;
			display:block;
			float: right;
		}
	}

	.para-detailed {
		border-radius: $radius;
		height: 100%;

		&:before {
			content: none;
		}
	}
}

.news-filter-btn {
	width: 175px;
	text-transform: none !important;
}

/* Blog listings */
.blog-featured {
	&.columns {
		margin-top: 2rem;
	}
	$padding: 0.618rem;

	background-color: #fff;

	&-header {
		padding-bottom: $padding;
		margin: 2rem 2rem;
		border-bottom: 1px solid $grey;
		margin-top: 4rem;
	}

	&-title {
		margin: 0 2rem;
	}

	&-date {
		text-align: right; 
	}

	&-lable {
		text-align: right;
		font-weight: 600;
	}

	&-content {
		margin: .5rem 2rem;
	}

	&-image {
		background-size: cover;
		width: 100%;
		height: 100%;
	}

	.blog-author {
		margin: 1rem 2rem 2rem 2rem;
	}
}



