.contact-us {
    &--form, &--map {
        padding: 2rem;
    }

    &--map {
        .contact-label {
            text-align: right;
        }

        address {
            margin-top: 1rem;
        }
    }

    &--form {
        .content {
            margin-bottom: 1rem;
        }

        .textarea {
            min-height: 250px;
        }

        .input {
            max-width: 320px;
        }
    }
}