@import "colors.scss";
@import "un_style_guide/vars.scss";



$blue: $brand-blue; // 
$blue-undp: #D5EDFB; // <- UNDP BLUE

$grey: $layout-reduced-contrast-light; // 70% black
$grey-dark: $layout-high-contrast-bg-light; // 80% black


$green: $brand-green; 
$red: $denotive-failure-light;


$grey-light: $layout-low-contrast-neutral-light; // 5% black
$grey-lighter: $grey-light;




/* Bulma's vars */


// fonts
$family-serif: 'Montserrat', sans-serif;
$family-sans-serif: 'Montserrat', sans-serif;
$body-color: $layout-reduced-contrast-light;


// buttons
$button-family: $family-sans-serif;

// navbar
$navbar-box-shadow-color: rgba(51,51,51,1);
$navbar-box-shadow-size: 0px 2px 21px -7px;
$navbar-height: 4rem;
$navbar-transition-duration: 1s;
$navbar-item-hover-background-color: $grey-light;
$navbar-item-active-color: $blue;
$navbar-background-color: $grey-light;

// footer
$footer-background-color: $grey-dark;
$footer-color: white;

// radius
// $radius-small: 0;
// $radius: 0;
// $radius-large: 0;


// links


$link: $interactive-primary;
$link-hover: $interactive-primary--1;
$link-focus: $interactive-primary--1;
$link-active: $interactive-primary--2;

$link-hover-border: transparent;
$link-focus-border: transparent;
$link-active-border: transparent;
