$team-member-border-color: darken($grey-light, 6%);

.team-outline-container {
	background-color: $grey-lighter;
}

.team-member {
	background-color: white;
	padding: 1rem;

	.team-listing & {
		box-shadow: none;
		background-color: transparent;
		border: none;

		.team-member-image {
			margin: auto;
		}
	}

	.team-listing-detail & {
		display:block;
		float: left;
		padding: 0 2rem 1rem 0;

		figure.image {
			width: 150px;
			height: 150px;
		}
	}

	.about-content & {
		background-color: transparent;
		border: none;
	}

	&-ghost {
		border: 1px solid $team-member-border-color;
		border: 1px solid $team-member-border-color;
		background-color: white;
		font-size: 1.2rem;
		font-weight: 500;
		text-align: center;
		min-height: 250px;
		position: relative;
	}

	&-see-more {
		display: block;
		width: 100%;
		text-align: center;
		position: absolute;
		top: 50%;
		padding: 1rem;
		transform: translateY(-59%);
	}

	.team-member-image {
		margin: 0 auto;
		margin-top: -50px;
		background-color: white;
		padding: 0.5rem;
		border: 1px solid $team-member-border-color;
		border-radius: $radius-rounded;
		width: 260px;
		height: 260px;

		.about-content & {
			margin-top: 0;
			background-color: transparent;
		}
	}

	.team-member-name {
		text-align: center;
		margin-top: 1rem;
		font-size: 1.1rem;
		margin-bottom: 0;
		font-family: $family-sans-serif;
		font-weight: 700;
	}

	.team-member-title {
		margin-bottom: 1rem;
		color: $grey;
		text-align: center;
		font-family: $family-sans-serif;
		font-weight: 600;
	}

	.team-member-description {
		text-align: justify;
		height: (1em * 1.5) * 8;
		overflow: hidden;
	}
}

// team page
.team-listing {
	$border: darken($grey-light, 10%);

	min-height: 100vh;

	&-member {
		background-color: $grey-light;
		border-top: 1px solid $border;
		padding: 3rem 0;
	}

	&-detail {
		padding: 0rem;
		text-align: justify;
	}

	&-para {
		padding-bottom: 1em * 1.5;
	}

	.team-member-contact {

		.content {
			text-align: center;
		}
	}
}

.team-member-homepage {

	// fullheight description section
	&, & .media-content, & .media-content .para-detailed {
		height: 100%;
	}

	margin-left: 45px;
	margin-top: 30px;
	padding-left: 0;
	padding-bottom: 0;


	.team-member-title {
		margin-bottom: 1rem;
		font-family: $family-sans-serif;
		font-weight: 600;
	}

	figure {
		width: 170px;
		height: 170px;
		margin-left: -72px;
		margin-top: -40px;
		padding: 0.5rem;
		background-color: $grey-lighter;
		border-radius: 50%;
		box-sizing: border-box;
	}
}

.team-member-description {
	text-align: justify;
}