.navbar {
      transition-property: background-color, box-shadow, transform;
      transition-duration: $navbar-transition-duration;

      font-family: $family-sans-serif;
	
      background-color: rgba(#fff, 1);

      .navbar-brand img {
            max-height: none;
            height: 5.8rem;
      }

      .navbar-link:not(.is-arrowless) {
            padding-right: 2em;

            &::after {
                  border-width: 2px;
                  width: 0.5em;
                  height: 0.5em;
                  border-color: $layout-high-contrast-bg-light;
            }
      }

      .navbar-menu {
            text-transform: uppercase;
						font-weight: 600;
      }

      // top nav items either with or without a dropdown
      .navbar-end {
            align-items: flex-end;

            > a.navbar-item,
            > .navbar-item > a.navbar-link {
                  transition-property: color;
                  transition-duration: 0.5s;
                  flex-direction: column;
                  margin: 0;

            }

            > .navbar-item {
                  min-width: 85px;
                  
                  .icon {
                        margin-bottom: 0.3rem;
                        transition: color;
                        color: #747474;
                        display: block;
                        margin: 0 auto;
                        width: auto;
                        height: auto;
                        font-size: 1.2rem;
                  }

                  &:hover .icon {
                        color: $green;
                  }
            }
      }

      .navbar-dropdown .navbar-item {
            transition-property: background-image, padding, color;
            transition-duration: 500ms;
            padding-right: 1rem + 0.5rem;
            padding-left: 1rem;
						// font-family: $family-serif;
						// font-weight: bold;

            &:not(.is-active):hover {
                  // @extend .texture;
                  padding-left: 1rem + 0.5rem;
                  padding-right: 1rem; 
                  color: $grey-light;
                  background-color: $green;
            }
      }
}

// navbar logo transparency
.logo {
      transition-property: opacity;
      transition-duration: $navbar-transition-duration;
      opacity: 1;

      // .navbar.navbar-transparent & {
      //       opacity: 0;
      //       transition-property: opacity;
      //       transition-duration: $navbar-transition-duration;
      // }
}

a.navbar-item, .navbar-item > .navbar-link {
      
      border-top: 4px solid transparent;
      border-bottom: 4px solid transparent;

      &.is-active {
            border-bottom: 4px solid $green;
            border-top: 4px solid transparent;
            color: $body-color;
      }

      .has-dropdown & {
            border: none;

            &.is-active {
                 background-color: $green !important;
                 color: $grey-light;
                  padding-left: 1rem + 0.5rem;
                  padding-right: 1rem; 
            }
      }

}

// dropdown animation
div.navbar-item.has-dropdown {

      &:hover > .navbar-dropdown {
            animation-name: menu-show; 
            animation-duration: 500ms;
            animation-fill-mode: forwards;
      }
      
}
