$reveal-animation-duration: 1.4s;

.hero-homepage {
    position: relative;

    background-image:
        url(../images/barley.jpg);

    background-size:
        cover;

    background-clip: border-box;
    background-repeat: no-repeat;

    background-position:
        top 0% left 0%; 

    min-height: 60vh;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: center;
}

.hero-scene {
    position: fixed;
    top: 0;
    width: 100vw;
    height: 100vh;
    z-index: -1;

    .background-layers {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;

        .layer {
            width: 100%;
            height: 100%;
            position: absolute;
            opacity: 0;
            background-size: cover;
            transition-property: all;
            transition-duration: 5s;

            &.active {
                opacity: 1;
                background-size: cover;
                transition-property: all;
                transition-duration: 5s;
            }
        }
    }

    .clouds {
        background-image: url(../images/clouds2.png);
        background-repeat: repeat-x;
        background-size: 1600px auto !important;
        background-position: 0 0;
        animation: moving-clouds 180s linear infinite;
        height: 450px;
    }

    .brightner {
        background-color: rgba(255, 255, 255, 0.4);
    }
}

.project-background {
    color: black;
}

.hero-columns {
    width: 80%;
    margin: 0 auto;
}



@keyframes moving-clouds {
    0% {
        background-position:0 100%
    }

    100%{
        background-position:-1600px 100%
    }
}


// --------------------------------------  CONVENTIONS  ----------------------------------------
.conventions-strip { 
    background-color: rgba(white, 0.5);
    color: rgb(51, 51, 51);
    clear: both;
}

.conventions-container {
    background-color: rgba(white, 1);
}

.convention {
    padding: 1.5rem;
}


// --------------------------------------------- White strig ---------------------------------------
.hero-tagline {
    position: absolute;
    width: 100%;
    bottom: 0;


    &-content {
        margin-top: 9rem;
        background-image: linear-gradient(
            transparent,
            rgba(black, 0.5),
            rgba(black, 1),
        );
    }
}

// side using golden ratio measurements
.hero-white-backdrop {
    position: absolute;
    height: 100%;
    width: 41.5%;
    background-color: rgba($grey-light, 0.95);
}

.hero-black-backdrop {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba($grey-dark, 0.3);
}


h2.tagline-heading {
    font-size: ms(3);
    color: $green !important;
    padding: 1rem;
    margin: 1rem;
    margin-left: 0;
    margin-bottom: 0 !important;
    line-height: 120%;

    span {
        display: block;
        margin-bottom: 0.1em;
    }
    
    font-weight: 400;
    strong {
        box-sizing: content-box;
        text-align: center;
        display: inline-block;
        padding: 0.1em;
        width: 1em;
        height: 1em;
        border-radius: 50%;
        background-color: $green;
        font-weight: 700;
        color: $grey-light !important;
        text-shadow: none;
        margin-right: 0.1em;
        line-height: 100%;
        
    }
}

.tagline-desc {
    color: white;
    font-weight: 700;
    border-radius: $radius;
    font-size: 1.2rem;
    text-shadow: 4px 2px 1px $grey-dark;
    letter-spacing: 2px;
    text-align: center;
    padding-bottom: 2rem; // to mirror spacing below (.section's padding)


    
    transition-property: box-shadow, border;
    transition-duration: $reveal-animation-duration;
    .button {
        opacity: 1;
        transition-property: opacity;
        transition-duration: $reveal-animation-duration;
    }
    &.in-animation {
        // @include box-shadow(1);

        transition-property: box-shadow, border;
        transition-duration: $reveal-animation-duration;
        .button {
            opacity: 0;
            transition-property: opacity;
            transition-duration: $reveal-animation-duration;
        }
    }
}

.tagline-logos {
        display: block;
        margin-top: 1em;
        margin-left: 2em;
        margin-bottom: 2em;
        img {
            display: inline-block;
            margin-right: .5em;
            height: 60px;
            width: auto;
        }
}
