.section {
    padding: 2rem 1rem;
}

.pad-left {
    padding-left: 1rem;
}

.pad-right {
    padding-right: 1rem;
}

.pad-less {
    padding: 0 !important;
}

.m-less {
    margin: 0 !important;
}

.m-top {
    margin-top: 1rem;
}

.m-bottom {
    margin-bottom: 1rem;

    &-2x {
        margin-bottom: 2rem !important;
    }
}

.full-height {
    min-height: 100%;

    &-fixed {
        height: 100%;
    }
}

.fill-height {
    display: flex;

    & > div {
        flex-grow: 1;
        flex-shrink: 1;
        flex-basis: auto; // initial size of the flex item
    }
}