.partners {
	background-color: white;
}

.partner-logo {
	.footer & {
		display: inline-block;
		margin-left: 1.2rem;
		padding: .2rem;

		img {
			height: 75px;
			width: auto;
		}

		&.state-emblem, &.gef {
			background: white;
		}

	}
}


.partner-logos-container {

	.footer & {
		margin-bottom: 2rem !important;
		padding-bottom: 1rem;
		border-bottom: 1px solid $border;
	}
}