.goal-outline {

    // over-riding .columns property for adding 
    // space at the bottom except for the last
    // .columns
    margin-bottom: 0 !important;

    .columns {
        margin-bottom: 0 !important;
    }

    .desc {
        padding: 2rem;
        padding-top: 0;
        min-height: 250px;

        h2 {
            text-align: justify;
        }
    }

    .graphic {
        background-image: url(../images/footer_lodyas.png);
        display: flex;
        justify-content: center;
        align-items: center;
        height: 100%;

        img {
            max-width: 75%;
        }
    }

    .desc {
        background-color: white;
        position: relative;
        
        .title {
            padding: 1rem .25rem;
            padding-bottom: .5rem;
            margin-bottom: 0;
        }

        .content {
            padding: 1rem .25rem;
        }

        // modifiers
        &.pull-up {
            @extend .box;
            // margin-top: -300px;
            box-shadow: 10px 5px 21px -8px rgba(#333333, 0.4);
            
        }

        .padded {
            padding: 2rem;
            padding-bottom: 0;
        }
    }

    // Caret
    .desc::before {
        content: '';
        width: 0;   
        height: 0; 
        border-top: 30px solid transparent;
        border-bottom: 30px solid transparent;
        position: absolute;
        top: 50%;
        transform: translate(0, -50%);
    }

    &.orientation-2 .desc::before {
        border-left: 30px solid $grey-dark;
        left: 0;
    }

    &.orientation-1 .desc::before {
        border-right: 30px solid $grey-dark;
        right: 0;
    }
}



.goal-outline-container {
    @include clearfix;
    background-color: $grey-lighter;
}

.joint-initiative-logos {
    img {
        max-width: 70%;
        display: block;
        margin: 0 auto;
    }
}