.breadcrumb {
    text-transform: capitalize;
    font-size: ms(0);
    color: $grey;
    font-family: $family-sans-serif;
    font-style: normal;

    display: inline-block;
    padding: 0.5em;
    background-color: $grey-light;
    border-radius: 1em;
    opacity: 0.8;

    li + li::before {
        color: $grey;
    }
}