.menu-label {
    font-family: $family-sans-serif;
    font-weight: 700;
}

.menu-list {
    a {
        font-size: 0.95rem;
        font-family: $family-sans-serif;
        font-weight: 400;
    }
}



.sub-menu {
    $sub-sub-menu-bg: #fff;
    $sub-menu-bg: $grey-light;

    height: 100%;

    > .menu-list > li {
        > a {
            background: $sub-menu-bg;
            color: $grey-dark;
            &:after {
                font-family: FontAwesome;
                content: "\f04b";
                display: inline-block;
                margin-right: 10px;
                color: transparent;
                float: right;
            }

            &.is-active {
                font-weight: 600;
                &:after {
                    color: $green;
                }
            }

            &:not(.is-active):hover {
                background-color: $green;
                color: white; 
                &:before {
                    color: $grey-light;
                }
            }
        } 
    }

    li ul {
        background: white;
        padding-left: 0;
        margin-top: 0;
        margin-bottom: 0;

        a { // sub menu links
            color: $grey;
            background: $sub-sub-menu-bg;
            &:hover {
                background: $sub-sub-menu-bg;
                &:before {
                    color: $grey-dark;
                }
            }

            &:before {
                font-family: FontAwesome;
                content: "\f054";
                display: inline-block;
                margin-right: 10px;
                color: transparent;
            }
        }
    }
}