.impacts-outline-container {
    @extend .is-clearfix;
    background-color: lighten($blue-undp, 0%);

    .content {
        text-align: justify;
    }

    .impact-icon {
        text-align: center;
        padding: 0.5rem;
    }

    .title {
        text-align: center;
    }
}

.impact-grid 
{
    background-color: $grey-light;

    .column {
        position: relative;
        padding-bottom: 3rem;
        background: white;
        border: 1px solid $grey-light;


        .impact-detail-btn {
            position: absolute;
            right: 0;
            bottom: 0;
        }
    }

    .content {
        text-align: justify;
    }
}