/**

Colors

1. Blue is used as primary color
	- page elements 
	- not for text or background

2. Light grey
	- page elements such as borders and backgrounds
	- not for text

3. Dark grey
	- Body text (main text color)

4. Grey
	- Site title
	- Navbars
	- Subheadings
*/
$blue: #0265b5; // 

$blue-undp: #D5EDFB; // <- UNDP BLUE

$grey: #4D4D4D; // 70% black

$grey-dark: #272727; // 80% black

$grey-light: #F2F2F2; // 5% black

$green: #75791e; 

h1,
h2,
h3,
h4,
h5,
h6 {
	font-weight: 900;
	font-family: $family-sans-serif;
}


$button-family: $family-sans-serif;