a.offset {
    display: block;
    visibility: hidden;
    position: relative;
    top: -10px;
}

.card-hover {
    @include box_shadow(1);
    transition-property: box-shadow, transform;
    transition-duration: 0.5s;
    
    &:hover {
        @include box_shadow(4);
        transition-property: box-shadow, transform;
        transition-duration: 0.5s;
        transform: translateY(-0.618rem);
    }
}