.work-container {
	background-color: $grey-light;
}

.story {
	margin-bottom: 1.618rem;

 	& &-heading {
		margin: 0;
	}

	& &-image {
		margin-bottom: 1rem;
	}

	& &-date {
		color: $grey;
		font-size: ms(-1);
		margin-bottom: 1rem;
	}

}

.publication, .report {
	display: block;
	overflow: hidden;
	position: relative;
	height: 100%;

	@include box-shadow(1);
	transition-property: box-shadow, transform;
	transition-duration: 500ms;

	.card-footer {
		position: absolute;
		bottom: 0;
		background-color: $grey-light;
		text-align: center;
		width: 100%;		

		transform: translateY(100px);
		transition-property: transform;
		transition-duration: 500ms;
	}

	&:hover {
		@include box-shadow(4);
		transform: translateY(-0.618rem);
		transition-property: box-shadow, transform;
		transition-duration: 500ms;

		.card-footer {
			transform: translateY(0px);
		}
	}
}

.report {
	.card-footer {
		position: static;
		background-color: transparent;
		text-align: left;
		transform: translateY(0);
	}

	&:hover .card-footer {
		color: $blue;
	}
}