.tag, a.tag {
    font-family: $family-sans-serif;
    font-weight: 500;

    transition-property: box-shadow;
    transition-duration: 500ms;
    @include box-shadow(1);
    &:hover {
        transition-property: box-shadow;
        transition-duration: 500ms;
        @include box-shadow(2);
        
        text-decoration: none;
    }
}