$tagline-bg-color: #163F44;

.tagline {
    .hero.is-primary & {
        min-height: 1em * 3 + 2em;
        color: white;

        text-transform: capitalize;

        letter-spacing: 0.05em;
        margin: 0.8rem;

        > span {
    	box-shadow: $tagline-bg-color -0.5em -0.5em 0px, $tagline-bg-color 0.5em -0.5em 0px, $tagline-bg-color -0.5em 0.5em 0px, $tagline-bg-color 0.5em 0.5em 0px;

	    background: $tagline-bg-color;

	    	> span {
	    		color: #ffffff;
    			position: relative;
    			text-shadow: none;
	    	}
        }

        // making it a block
        // background-color: rgba($grey-dark, 0.7);
        // padding: 1rem;
        text-shadow: none;
    }
}
